<template>
  <div class="go-top" @click="gotoTop">
    
    <img src="../assets/img/goTop.png" alt="">
  </div>
</template>

<script>
export default {
  data(){
    return({
      like:false
    })
  },
  methods: {
    gotoTop() {
      document.documentElement.style.transition = 'all .3s'
      let top = document.documentElement.scrollTop || document.body.scrollTop
      // 实现滚动效果
      const timeTop = setInterval(() => {
        document.body.scrollTop = document.documentElement.scrollTop = top -= 50
        if (top <= 0) {
          clearInterval(timeTop)
        }
      }, 10)
    }
  }
}
</script>

<style scoped>
.go-top {
  position: fixed;
  right: 30px;
  bottom: 50px;
}
</style>