      <van-share-sheet v-model="showShare" title="立即分享给好友" :options="options" />
    </div>
  </div>
</template>

<script>
import goTop from '@/components/goTop'
export default {
  components: {
    goTop
  },
  data() {
    return ({
      article: JSON.parse(this.$route.params.article),
      value: 5,
      showShare: false,
      options: [
        [
          { name: '微信', icon: 'wechat' },
          { name: '朋友圈', icon: 'wechat-moments' },
          { name: '微博', icon: 'weibo' },
          { name: 'QQ', icon: 'qq' },
        ],
        [
          { name: '复制链接', icon: 'link' },
          { name: '分享海报', icon: 'poster' },
          { name: '二维码', icon: 'qrcode' },
          { name: '小程序码', icon: 'weapp-qrcode' },
        ],
      ],
    })
  },
  created() {
    if (localStorage.getItem('userInfo')) {
      let collectList = JSON.parse(localStorage.getItem('collectList')) || []
      for (let i = 0; i < collectList.length; i++) {
        if (collectList[i].title == this.article.title) this.article.like = collectList[i].like
      }
    }
  },
  methods: {
    collect() {
      let userInfo = localStorage.getItem('userInfo')
      if (userInfo == '' || userInfo == null) {
        this.$toast.fail('你还未登录~')
        return
      } else {
        this.article.like = !this.article.like
        if (this.article.like) {
          this.$store.state.collectList.push(this.article)
          localStorage.setItem('collectList', JSON.stringify(this.$store.state.collectList))
          this.$toast.success('收藏成功~')
        } else {
          this.$store.state.collectList.forEach((item, index) => {
            if (item.title == this.article.title) this.$store.state.collectList.splice(index, 1)
          })
          localStorage.setItem('collectList', JSON.stringify(this.$store.state.collectList))
          this.$toast.success('取消收藏~')
        }
      }
    },
  }
}
</script>

<style scoped>
.collect {
