  color: #fff;
  width: 120px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  margin: 20px auto;
}

.article {
  margin-top: 46px;
  text-align: center;
  background: rgb(250, 241, 242);
  min-height: calc(100vh - 50px);
  padding-top: 20px;
}

.info {
  display: flex;
  padding: 20px;
  justify-content: center;
}

.time {
  margin-left: 15px;
}

.content {
  margin: 20px;
  text-align: left;
  line-height: 30px;
  letter-spacing: 2px;
}
.content  div{
  margin:10px 0;
}
</style>